.page-back {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    padding: 30px;
    gap: 50px;

    background-image: linear-gradient(black,whitesmoke);
}

.page-hdr {
    font-size: 2em;
}

.page-subhdr {
    font-size: 1em;
}

.page-cont {
    display: flex;
    flex-direction: row;

    width: 80vw;
    max-width: 2000px;
    padding: 30px;
    box-sizing: border-box;
    
    background-color: white;
    border: 2px solid black;
    border-radius: 20px;
}

.page-sidebar {
    display: flex;
    flex-direction: column;

    width: 30vw;
    max-width: 400px;

    padding-right: 20px;

    border-right: 2px solid black;
}

.page-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    width: 100%;
}

.page-topic {
    padding: 30px;
}

.sep-top {
    border-top: 2px solid rgba(0, 0, 0, 0.334);
}

@media screen and (max-width:1250px) {
    .page-back {
        padding: 0px;
    }
    .page-cont {
        left: 0;
        flex-direction: column;
        box-sizing: border-box;
        width: 100%;
        padding: 5px;
        border-radius: 0px;
    }

    .page-sidebar {
        justify-content: center;
        align-items: center;

        box-sizing: border-box;
        width: 100%;
        max-width: 100%;

        border-right: none;
        border-bottom: 2px solid rgba(0, 0, 0, 0.334);
        background-color: white;
    }

    .page-content {
        width: 100%;
    }

    .page-hdr {
        font-size: 1.5em;
    }

    .page-subhdr {
        display: none;
        visibility: hidden;
    }

    #page-title {
        text-align: center;
    }
}