#prob-state a {
    font-size: 20px;
}

#prob-state p {
    font-size: 18px;
}

.prob-so-sect p {
    font-size: 18px !important;
}

.prob-so-sect ul li {
    font-size: 17px;
}

.prob-so-sect ol li {
    font-size: 17px;
}

@media screen and (max-width:1250px) {
    #prob-state a {
        font-size: 18px;
    }

    #prob-state p {
        font-size: 16px;
    }

    .prob-so-sect ul li {
        font-size: 15px;
    }
    
    .prob-so-sect ol li {
        font-size: 15px;
    }

    .prob-so-sect p {
        font-size: 16px !important;
    }
}