.cont-cont {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    padding: 30px;
    gap: 50px;

    background-image: linear-gradient(black,whitesmoke);
}

.topic-cont {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 30px;
    gap: 30px;

    border: 2px solid black;
    border-radius: 20px;

    background-color: whitesmoke;
    transition: all .3s ease;
}

.topic {
    width: 75vw;
    max-width: 340px;

    height: fit-content;

    border: 2px solid black;
    border-radius: 10px;

    padding: 20px;
    padding-top: 0px;

    background-color: white;

    white-space: wrap;
    word-wrap: break-word;
}

.topic h5 {
    margin: 10px;
}

.lnk {
    display: inline-block;
    position: relative;
    color: black;
    text-decoration: none;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
}

.lnk:hover {
    color: #738290;
}

.lnk:after {
    content: 'âž¤';
    color: #738290;
    position: absolute;
    opacity: 0;
    right: -25px;
    transition: 0.3s;
}

.lnk:hover {
    padding-right:28px;
}

.lnk:hover:after {
    opacity: 1;
    right: 10px;
}

@media screen and (max-width:500px) {
    .topic-cont {
        background-color: transparent;
        border: none;
        padding: 0px;
    }
}