.table-wrap {
    width: 100%;
    max-width: 1000px;

    overflow-x: auto;
    overflow-y: auto;

    border: 1px solid black;
    box-shadow: 0px 5px 5px #ccc;
    border-radius: 15px;
    background-color: transparent;
}

.table {
    table-layout: auto;

    width: 100%;
    max-width: 1000px;

    border-radius: 15px;
    border-collapse: collapse;
    background-color: whitesmoke;

}

.table-head {
    background-color: rgba(0, 0, 0, 0.24);
    border-bottom: 1px solid black;
}

.table-wrap th {
    text-align: left;
    padding: 0.8rem;
    font-weight: 400;
}

.table-head th {
    font-family: "Funnel Display", sans-serif;
    font-weight: 700 !important;
}

.table-item {
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
}

.table-item:hover {
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;

    background-color:rgb(233, 230, 230);
}

.table-col {
    width: 1px;
    white-space: nowrap;
}

.easy {
    width: fit-content;
    padding-left: 10px;
    padding-right: 10px;

    font-size: 15px;
    color: white;
    background-color: darkseagreen;
    border-radius: 15px;
}

.med {
    width: fit-content;
    padding-left: 10px;
    padding-right: 10px;

    font-size: 15px;
    color: white;
    background-color: goldenrod;
    border-radius: 15px;
}

.hard {
    width: fit-content;
    padding-left: 10px;
    padding-right: 10px;

    font-size: 15px;
    color: white;
    background-color: rgb(190, 93, 49);
    border-radius: 15px;
}

@media screen and (max-width:1250px) {
    .table-item {
        font-size: 15px;
    }
}