.navb-wrap {
    display: flex;
    justify-content: center;
    align-items:center;

    height: 70px;

    background-color: transparent;

    position: sticky;
    top: 10px;
    z-index: 9;
}

.navb {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    height: 100%;
    width: 70%;
    max-width: 800px;

    padding: 0px 30px 0px 26px;

    background-color: whitesmoke;
    padding: 0 1rem;
    border: 1px solid darkgray;
    border-radius: 35px;
}

.navb-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0px;
    margin: 0px;

    width: 100%;
    max-width: 750px;
}

.navb-item {
    text-decoration: none;
    list-style: none;
}

.iconBt {
    text-decoration: none;
    transition: filter 300ms;
}

.iconBt:hover {
    filter: brightness(1.2);
}

#nav-img {
    width: 65px;
}

#nav-txt {
    font-size: 17px;
    font-family: "Funnel Display", sans-serif; 
    color: black;
    opacity: 0.8;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
}

#nav-txt:hover {
    opacity: 0.5;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
}

@media screen and (max-width: 1250px) {
    .navb {
        width: 90%;
        padding: 0px 30px 0px 26px;
    }
}