.pop {
    display: flex;
    flex-direction: column;

    width: 60%;
    height: 60%;

    padding: 10px;

    background-color: whitesmoke;
    border: 1px solid black;
    border-radius: 15px;
    overflow-y: auto;
}

.pop-bt-wrap {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    position: sticky;
    top: 0%;
}

.pop-back {
    display: flex;
    justify-content: center;
    align-items: center;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, 0.385);

    z-index: 10;
}

#pop-bt {
    border:none;
    border-radius: 100px;

    color: black;
    font-size: 25px;
    font-weight: 700;

    transition: all 0.2s ease;
    -webkit-transition: all 0.2s ease;
}

#pop-bt:hover {
    cursor: pointer;
    background-color:lightgrey;
}

#pop-txt {
    transition: all 0.2s ease;
    -webkit-transition: all 0.2s ease;
    width: fit-content;
    width: 1px;
    white-space: nowrap;
}

#pop-txt:hover {
    cursor: pointer;
    opacity: 0.5;
}

#pop-txt-wrap {
    width: fit-content;
}

#pop-content-wrap {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;

    width:100%;

    padding: 50px;
    padding-top: 20px;
}

#pop-hdr p {
    font-size: 20px;
}

#pop-hdr h3 {
    font-size: 34px;
}

.pop-hdr-txt {
    margin: 0px;
    white-space: wrap;
    word-wrap: break-word;
}

#pop-sect-wrap {
    width: 100%;

    padding-top: 30px;
    padding-bottom: 30px;

    white-space: wrap;
    word-wrap: break-word;
    
    border-bottom: 2px solid rgba(0, 0, 0, 0.385);
}

#pop-sect-wrap h5 {
    margin: 0px;
    font-size: 28px;
}

#pop-sect-wrap p {
    margin: 0px;
    font-size: 20px;
}

@media screen and (max-width:1250px) {
    .pop {
        width: 90%;
        height: 60%;
    }

    #pop-hdr p {
        font-size: 15px;
    }

    #pop-hdr h3 {
        font-size: 25px;
    }

    #pop-sect-wrap h5 {
        font-size: 20px;
    }

    #pop-sect-wrap p {
        font-size: 15px;
    }

    #pop-content-wrap {
        padding: 20px;
        padding-top: 0px;
    }
}