.banner-back {
    position: relative;
    display: flex;
    width: 100%;
    max-height:350px;
    box-sizing: border-box;
    height: 34%;
}

.banner-img-cont {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    box-sizing: border-box;
    width: 100%;
    overflow: hidden;
}

.banner-img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 500px;
}

.banner-temp {
    position: relative;
    width:100%;
    max-height:350px;
    height:34%;
    background-color:black;
    overflow:hidden;

    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
}

.banner-temp-cont {
    font-family: "Do Hyeon", serif;
    font-weight: 500;
    background: linear-gradient(to right, whitesmoke,#a0acad);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}